.contain {
  height: 550px;
  margin: 250px auto;
  position: relative;
  width: 250px;
}
.box {
  -webkit-animation: 14s ease 0s normal none infinite running rotate;
          animation: 14s ease 0s normal none infinite running rotate;
  height: 240px;
  margin: 0 auto;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 240px;
}
.face {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background-color: #fae48c;
  height: 240px;
  opacity: 0.6;
  position: absolute;
  width: 240px;
}
.box .one {
  background-color: #fae48c;
  -webkit-transform: rotateX(90deg) translateZ(120px);
          transform: rotateX(90deg) translateZ(120px);
}
.box .two {
  background-color: #9900CC;
  -webkit-transform: translateZ(120px);
          transform: translateZ(120px);
}
.box .three {
  background-color: #009900;
  -webkit-transform: rotateY(90deg) translateZ(120px);
          transform: rotateY(90deg) translateZ(120px);
}
.box .four {
  background-color: #66CCFF;
  -webkit-transform: rotateY(180deg) translateZ(120px);
          transform: rotateY(180deg) translateZ(120px);
}
.box .five {
  background-color: #CC0000;
  -webkit-transform: rotateY(-90deg) translateZ(120px);
          transform: rotateY(-90deg) translateZ(120px);
}
.box .six {
  background: none repeat scroll 0 0 #fae48c;
  -webkit-transform: rotateX(-90deg) translateZ(120px) rotate(180deg);
          transform: rotateX(-90deg) translateZ(120px) rotate(180deg);
}
@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate3d(1, 1, 1, 360deg);
            transform: rotate3d(1, 1, 1, 360deg);
  }
}
@keyframes rotate {
  100% {
    -webkit-transform: rotate3d(1, 1, 1, 360deg);
            transform: rotate3d(1, 1, 1, 360deg);
  }
}
